//button-cross
.button-cross-wrap {
	position: relative;
}

button {
	&.button-cross {
		position: absolute;
		top: 0%;
		left: 10px;
		transform: translateY(-55%);
		width: 180px;
		height: 60px;
		text-align: center;
		line-height: 60px;
		text-decoration: none;
		text-transform: uppercase;
		letter-spacing: 6px;
		border: 2px solid #a133b4;
		font-size: 24px;
		color: #a133b4;
		transition: 0.5s;
		overflow: hidden;
	}

	&.button-cross:hover {
		color: #fff;
	}

	&.button-cross span {
		position: absolute;
		width: 100%;
		height: 100%;
		background: rgba(161, 51, 180, 0.5);
		transition: 0.5s;
		z-index: -1;
	}

	&.button-cross span:nth-child(1) {
		top: -100%;
		left: -100%;
	}

	&.button-cross:hover span:nth-child(1) {
		top: 0;
		left: 0;
	}

	&.button-cross span:nth-child(2) {
		bottom: -100%;
		left: -100%;
	}

	a:hover span:nth-child(2) {
		bottom: 0;
		left: 0;
	}

	&.button-cross span:nth-child(3) {
		top: -100%;
		right: -100%;
	}

	a:hover span:nth-child(3) {
		top: 0;
		right: 0;
	}

	&.button-cross span:nth-child(4) {
		bottom: -100%;
		right: -100%;
	}

	&.button-cross:hover span:nth-child(4) {
		bottom: 0;
		right: 0;
	}
}

//button-text-annimate
.button-text-annimate-wrap {
	position: relative;
	height: 60px;
	display: inline-block;
}

.button-text-annimate {
	background: white;
	padding: 20px;
	text-align: center;
	display: flex;
	justify-content: center;

	span {
		display: inline-block;
		margin-right: 10px;
		text-transform: uppercase;
		transition: 0.25s cubic-bezier(0.5, -1, 0.5, 2);
		opacity: 0;
		transform: translate(0, -20px);
	}

	&:before {
		content: attr(data-text);
		position: absolute;
		width: 100%;
		left: 0;
		transition: 0.25s cubic-bezier(0.5, -1, 0.5, 2);
		text-transform: uppercase;
		letter-spacing: 3.5px;
		opacity: 1;
		transform: translate(0, 0px);
	}

	&:hover:before,
	&:focus:before {
		opacity: 0;
		transform: translate(0, 20px);
	}

	&:hover span,
	&:focus span {
		opacity: 1;
		transform: translate(0, 0);
	}

	&:hover span:nth-child(1),
	&:focus span:nth-child(1) {
		transition-delay: 0.025s;
	}

	&:hover span:nth-child(2),
	&:focus span:nth-child(2) {
		transition-delay: 0.05s;
	}

	&:hover span:nth-child(3),
	&:focus span:nth-child(3) {
		transition-delay: 0.075s;
	}

	&:hover span:nth-child(4),
	&:focus span:nth-child(4) {
		transition-delay: 0.1s;
	}

	&:hover span:nth-child(5),
	&:focus span:nth-child(5) {
		transition-delay: 0.125s;
	}

	&:hover span:nth-child(6),
	&:focus span:nth-child(6) {
		transition-delay: 0.15s;
	}
}

