// btn atom
.button-4 {
	background-color: #000;
	padding: 20px;
}

@keyframes atom {
	0% {
		transform: translateX(0) rotate(0);
	}

	30% {
		transform: translateX(var(--tr-X)) rotate(0);
	}

	50% {
		transform: translateX(var(--tr-X)) rotate(180deg);
	}

	80% {
		transform: translateX(0) rotate(180deg);
	}

	100% {
		transform: translateX(0) rotate(360deg);
	}
}

:root {
	--bg: #3C465C;
	--primary: #78FFCD;
	--solid: #fff;
	--btn-w: 10em;
	--dot-w: calc(var(--btn-w)*.2);
	--tr-X: calc(var(--btn-w) - var(--dot-w));
}

.btn-atom {
	position: relative;
	margin: 0 auto;
	width: var(--btn-w);
	color: var(--primary);
	border: .15em solid var(--primary);
	border-radius: 5em;
	text-transform: uppercase;
	text-align: center;
	font-size: 1.3em;
	line-height: 2em;
	cursor: pointer;

	&:hover .dot,
	&:focus .dot {
		animation: atom 4s infinite linear;
		display: block;
	}
}

.dot {
	content: '';
	position: absolute;
	top: 0;
	width: var(--dot-w);
	height: 100%;
	border-radius: 100%;
	transition: all 300ms ease;
	display: none;

	&:after {
		content: '';
		position: absolute;
		left: calc(50% - .4em);
		top: -.4em;
		height: .8em;
		width: .8em;
		background: var(--primary);
		border-radius: 1em;
		border: .25em solid var(--solid);
		box-shadow: 0 0 .7em var(--solid),
			0 0 2em var(--primary);
	}
}

//btn-mat
.btn-mat {
	$width-close-btn: 40px;
	position: relative;
	background: #3D4C53;
	margin: 20px auto;
	width: 200px;
	height: 50px;
	overflow: hidden;
	display: flex;
	transition: .2s all;
	cursor: pointer;
	border-radius: 3px;
	box-shadow: 0px 1px 2px rgba(0, 0, 0, .2);

	&:hover .btnTwo {
		transform: translateX(0);
	}

	&:hover .btnText {
		transform: translateX($width-close-btn);
		width: calc(100% - #{$width-close-btn});
	}

	.btnTwo {
		display: block;
		position: absolute;
		top: 0;
		bottom: 0;
		width: 40px;
		transform: translateX(-$width-close-btn);
		left: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		background: #26A69A;
		transition: .3s all;
		justify-items: flex-start;

		.btnText2 {
			display: block;
			color: #FFF;
		}
	}

	.btnText {
		display: block;
		color: white;
		transition: .3s;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
	}
}

// Ripple effect
.button-group2 {
	[anim="ripple"] {
		//  Customize effect by defining the following
		//  variables on the parent element
		//  --ripple-background: white;
		//  --ripple-opacity: 0.3;
		//  --ripple-duration: 600ms;
		//  --ripple-easing: linear;
		position: relative;
		overflow: hidden;

		&:before {
			content: '';
			position: absolute;
			display: block;
			background: var(--ripple-background, white);
			border-radius: 50%;
			pointer-events: none;
			//  position and size
			top: calc(var(--y) * 1px);
			left: calc(var(--x) * 1px);
			width: calc(var(--d) * 1px);
			height: calc(var(--d) * 1px);
			//  animated properties
			opacity: calc(var(--o, 1) * var(--ripple-opacity, 0.3));
			transition: calc(var(--t, 0) * var(--ripple-duration, 600ms)) var(--ripple-easing, linear);
			transform: translate(-50%, -50%) scale(var(--s, 1));
			transform-origin: center;
		}
	}

	//  Just some styling
	button {
		--ripple-background: white;
		--ripple-opacity: 0.3;
		--ripple-duration: 600ms;
		border: 1px solid darken(tomato, 20%);
		border-radius: 0;
		padding: 0.75em 3em;
		font-size: 1em;
		outline: none;
		display: inline-block;
		background: tomato;
		color: white;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		min-width: 200px;
		text-align: center;
		vertical-align: middle;
		cursor: pointer;
		user-select: none;
	}

	.button2 {
		--ripple-background: black;
		--ripple-opacity: 0.1;
		--ripple-duration: 600ms;
		display: inline-block;
		padding: 0.75em 3em;
		background: whitesmoke;
		color: black;
		text-decoration: none;
		border: 1px solid darken(whitesmoke, 10%);
		min-width: 200px;
		text-align: center;
		vertical-align: middle;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		transition: background-color 300ms;

		&:hover {
			background-color: darken(whitesmoke, 3%);
		}
	}

	.rainbow {
		--ripple-opacity: 1;
		--ripple-background: radial-gradient(circle at center, rebeccapurple, dodgerblue, olive, gold, orange, tomato);
		--ripple-duration: 2000ms;
		--ripple-easing: cubic-bezier(0, .5, .5, 1);
		background-color: skyblue;
		color: rgba(white, 0.7);
		transition: background-color 300ms, color 300ms;

		&:hover {
			background-color: lighten(skyblue, 10%);
			color: rgba(white, 1);
		}
	}
}

