// input-outline
.input-outline {
	position: relative;
	display: inline-block;

	input {
		width: 160px;
		color: white;
		font-size: inherit;
		font-family: inherit;
		background-color: hsl(236, 32%, 26%);
		padding: 0.35em 0.45em;
		border: 1px solid transparent;
		transition: background-color 0.3s ease-in-out;
	}

	input:focus {
		outline: none;
	}

	input::placeholder {
		color: hsla(0, 0%, 100%, 0.6);
	}

	span {
		position: absolute;
		background-color: #fc2f70;
		transform-origin: center;
		transition: transform 0.5s ease;
	}

	.bottom,
	.top {
		height: 1px;
		left: 0;
		right: 0;
		transform: scaleX(0);
	}

	.left,
	.right {
		width: 1px;
		top: 0;
		bottom: 0;
		transform: scaleY(0);
	}

	.top {
		top: 0;
	}

	.bottom {
		bottom: 0;
	}

	.left {
		left: 0;
	}

	.right {
		right: 0;
	}

	input:focus~.top,
	input:focus~.bottom {
		transform: scaleX(1);
	}

	input:focus~.left,
	input:focus~.right {
		transform: scaleY(1);
	}
}

//input-trace
.input-trace {
	position: relative;
	display: inline-block;

	input {
		width: 160px;
		color: white;
		font-size: inherit;
		font-family: inherit;
		background-color: hsl(236, 32%, 26%);
		padding: 0.35em 0.45em;
		border: 1px solid transparent;
		transition: background-color 0.3s ease-in-out;
	}

	input:focus {
		outline: none;
	}

	input::placeholder {
		color: hsla(0, 0%, 100%, 0.6);
	}

	span {
		position: absolute;
		background-color: #fc2f70;
		transition: transform 0.1s ease;
	}

	.bottom,
	.top {
		height: 1px;
		left: 0;
		right: 0;
		transform: scaleX(0);
	}

	.left,
	.right {
		width: 1px;
		top: 0;
		bottom: 0;
		transform: scaleY(0);
	}

	.bottom {
		bottom: 0;
		transform-origin: bottom right;
	}

	input:focus~.bottom {
		transform-origin: bottom left;
		transform: scaleX(1);
	}

	.right {
		right: 0;
		transform-origin: top right;
		transition-delay: 0.05s;
	}

	input:focus~.right {
		transform-origin: bottom right;
		transform: scaleY(1);
	}

	.top {
		top: 0;
		transform-origin: top left;
		transition-delay: 0.15s;
	}

	input:focus~.top {
		transform-origin: top right;
		transform: scaleX(1);
	}

	.left {
		left: 0;
		transform-origin: bottom left;
		transition-delay: 0.25s;
	}

	input:focus~.left {
		transform-origin: top left;
		transform: scaleY(1);
	}
}

//input-underline
.input-underline {
	position: relative;
	display: inline-block;

	input {
		width: 160px;
		color: white;
		font-size: inherit;
		font-family: inherit;
		background-color: transparent !important;
		border: 1px solid transparent;
		border-bottom-color: hsla(185, 100%, 62%, 0.3) !important;
	}

	input:hover {
		border-width: 0px !important;
		border-bottom-width: 1px !important;
	}

	input:focus {
		outline: none;
	}

	input::placeholder {
		color: hsla(0, 0%, 100%, 0.6);
	}

	span {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 1px;
		background-color: #3cefff;
		transform-origin: bottom right;
		transform: scaleX(0);
		transition: transform 0.5s ease;
	}

	input:focus~span {
		transform-origin: bottom left;
		transform: scaleX(1);
	}
}